@import url(https://fonts.googleapis.com/css2?family=Josefin+Sans:wght@400;700&display=swap);
*{margin:0;padding:0;box-sizing:border-box;font-family:"Josefin Sans", sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}*:focus{outline:none}*::-webkit-scrollbar{width:5px}*::-webkit-scrollbar-track{background:transparent}*::-webkit-scrollbar-thumb{background:rgba(54,54,54,0.6)}html{font-size:14px}.App{width:100vw;height:100vh;position:relative;overflow-x:hidden;background:#fafafa}.App.dark{background:#181824}.App .bg-img{position:absolute;top:0;left:0;width:100%;object-fit:cover;height:16rem}input,p{font-size:1.5rem}.singleTodo,.showTodo footer,.creation{padding:0 1rem}.checker,.roundUseless{width:26px;min-width:26px;height:26px;border-radius:50%;display:grid;place-items:center;margin:auto 0;margin-right:1rem;border:1px solid gray}.checker img,.roundUseless img{opacity:0}.actual-todo{max-width:560px;height:auto;margin:3rem auto;margin-bottom:7rem;padding:0 10px;position:relative}.actual-todo .head{display:flex;justify-content:space-between;align-items:center;width:100%}.actual-todo .head h1{color:white;font-size:3rem;letter-spacing:1.5rem}.actual-todo .body{width:100%}@media (max-width: 515px){.actual-todo{margin-bottom:12rem}}.creation{margin:2rem 0 1rem;height:64px;display:flex;box-shadow:0 1rem 1.5rem rgba(206,206,206,0.281);background:white;border-radius:5px;overflow:hidden}.creation input{flex:1 1 auto;height:100%;border:none;background:white;color:#25273c}.creation input.dark{background:#25273c;color:white}.creation.dark{box-shadow:0 1rem 1.5rem rgba(0,0,0,0.281);background:#25273c}.showTodo{height:auto;border-radius:5px;width:100%;background:white;box-shadow:0 1rem 1.5rem rgba(206,206,206,0.5)}.showTodo .body{display:flex;flex-direction:column-reverse}.showTodo.dark{box-shadow:0 1rem 1.5rem rgba(0,0,0,0.438);background:#25273c}footer{position:relative;height:64px;display:flex;justify-content:space-between;align-items:center}footer span.inst{position:absolute;top:calc(100% + 3rem);left:0;width:100%;text-align:center;font-size:1.4rem;opacity:0}footer li,footer span{color:#363636}footer ul{border-radius:5px;list-style:none;display:flex;justify-content:space-between;align-items:center}footer ul li{margin:0 0.5rem}footer ul li.filterValue{opacity:1 !important;color:#4d7de1}footer.dark li,footer.dark span{color:#ddd}footer .left,footer .filter li,footer .clearComp{cursor:pointer;opacity:0.5;font-weight:700}footer .left:hover,footer .filter li:hover,footer .clearComp:hover{opacity:1}@media (max-width: 515px){footer ul{position:absolute;top:calc(100% + 1rem);justify-content:space-around;left:0;height:64px;width:100%;background:white;box-shadow:0 1rem 1.5rem rgba(206,206,206,0.5)}footer ul.dark{background:#25273c;box-shadow:0 1rem 1.5rem rgba(0,0,0,0.438)}footer span.inst{top:calc(100% + 7rem)}}.singleTodo{min-height:64px;padding-top:1rem;padding-bottom:1rem;border-bottom:1px solid rgba(167,167,167,0.5);display:flex;align-items:center;justify-content:center}.singleTodo p{flex:1 1 auto}.singleTodo p.dark{color:white}.singleTodo p.checked{text-decoration:line-through}.singleTodo .checker,.singleTodo .cross{cursor:pointer}.singleTodo .cross{opacity:0}.singleTodo:hover .cross{opacity:1}.singleTodo .checker img{pointer-events:none}.singleTodo .checker.checked{background:linear-gradient(to right bottom, #6e96a0 40%, #c058f3) 60%}.singleTodo .checker.checked img{opacity:1;-webkit-transform:scale(1.4);transform:scale(1.4)}.singleTodo.checked{opacity:0.5}

